<template>
	<view class="container">
		<view class="segmented">
			<view :class="{item:true,active:active==index?true:false}" v-for="(item,index) in items" :key="index"
				@click="handleSegmented(index)">
				{{item}}
			</view>
		</view>
		<navigator class="box" v-for="(item,index) in lists" :key="index" hover-class="none"
			:url="'./show?id='+item.id">
			<view class="box-t">
				<view class="title">订单号：<text class="text">{{item.orderid}}</text></view>
				<view class="more">{{item.state_cn}}</view>
			</view>
			<view class="box-c">
				<view class="service-box">
					<view class="service">
						<image src="../../static/images/16197756346801619775668155.png" mode="scaleToFill"
							class="thumb">
						</image>
						<view class="info">
							<view class="title">
								{{item.service_name}} <view class="num">￥{{item.price}}</view>
							</view>
							<view class="time">预约时间：{{item.yuyuedate ? item.yuyuedate:''}} {{item.yuyuetime ? item.yuyuetime:''}}</view>
						</view>
					</view>		 
					<view class="service-buttons">
						<view class="btn">联系客服</view>
					</view>
				</view>
			</view>
		</navigator>
		<uni-load-more :status="more"></uni-load-more>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				active: 0,
				items: [
					'全部', '待接单', '待上门', '待核定', '待评价', '已完成', '已取消'
				],
				lists: [],
				page: 1,
				totals: 0,
				more: 'more'
			}
		},
		onLoad(option) {
			if (this.vuex_login == 0) {
				this.$u.route('pages/login/index');
				return
			}
			this.active = option.status
			this.getLists()
		},
		onReachBottom(e) {
			this.more = 'loading'
			this.page++
			if (this.page <= this.totals) {
				this.getLists()
			} else {
				this.$u.toast('没有数据了')
				this.more = 'noMore'
			}

		},
		onPullDownRefresh() {
			this.lists = []
			this.totals = 0
			this.page = 1
			this.active = 0
			this.getLists()

		},
		methods: {
			async getLists() {
				let res = await this.$u.get('/v2/order/index', {
					uid: this.vuex_user.id,
					status: this.active,
					page: this.page
				})
				this.more = 'more'
				this.totals = res.data.total_pages
				this.lists = this.lists.concat(res.data.data)
				if (this.totals == 0) {
					this.more = 'noMore'
				}
				uni.stopPullDownRefresh();
			},
			handleSegmented(index) {
				this.active = index
				this.lists = []
				this.page = 1
				this.totals = 0
				this.getLists()
			}

		}
	}
</script>

<style scoped lang="scss">
	.container {
		display: block;
		min-height: 100vh;
		background-color: #F8F8F9;
		position: relative;
	}

	.segmented {
		background-color: #fff;
		width: 100vw;
		display: flex;
		justify-content: space-around;
		align-items: flex-end;
		font-size: 28rpx;
		position: sticky;
		top: var(--window-top);
		z-index: 600;
		font-weight: bold;
		padding: 20rpx 0;
		color: #333
		
	}
	.segmented .item{
		padding-bottom:10rpx;
	}
	.segmented .item.active {
		color: #F65425;
		border-bottom: #F65425 2px solid;
		font-size: 34rpx;
	}

	.box {
		border-radius: 20rpx;
		box-shadow: 0 0 20rpx rgba(0, 0, 0, 0.1);
		margin: 20rpx;
		background-color: #fff;
	}

	.box .box-t {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 25rpx 0;
		margin: 0 20rpx;
		border-bottom: #eee 1px solid;
	}

	.box .box-t .title {
		font-size: 24rpx;
	}

	.box .box-t .title .text {
		color: #777
	}

	.box .box-t .more {
		font-size: 24rpx;
		color: #F65425
	}

	.service-box {
		margin: 20rpx;
		padding-bottom: 20rpx;
	}

	.service-box .service {
		display: flex;

	}

	.service-box .service .thumb {
		width: 120rpx;
		height: 120rpx;
		border-radius: 8rpx;
		margin-right: 20rpx;
	}

	.service-box .service .info {
		flex: 1;
		height: 120rpx;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	.service-box .service .info .title {
		font-size: 30rpx;
		display: flex;
		justify-content: space-between;
		margin-bottom: 8rpx;
		font-weight: bold;
	}

	.service-box .service .info .title .num {
		color: #F65425;
		font-weight: normal;
		font-size: 28rpx;
	}

	.service-box .service .info .time {
		color: #999999;
		font-size: 24rpx;
	}

	.service-box .service-tips {
		text-align: right;
		margin: 16rpx 0 30rpx 0;
		font-size: 28rpx;
	}

	.service-box .service-tips .red {
		color: #FF5A5F;
	}

	.service-box .service-buttons {
		border-top: #eee 1px solid;
		margin-top: 30rpx;
		padding-top: 20rpx;
		display: flex;
		justify-content: flex-end;

	}

	.service-box .service-buttons .btn {
		width: 160rpx;
		height: 60rpx;
		border: 1px solid #CECECE;
		background: #fff;
		border-radius: 34rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #333;
		font-size: 26rpx;
	}
	.service-box .service-buttons .btn.active {
		border: 1px solid #FF4400;
		color: #FF4400;
	}
</style>
